@font-face {
  font-family: "SecretCode";
  src: url(/static/media/Code-Regular.4987dcae.woff);
  font-weight: normal;
  font-style: normal;
}

* {
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.SecretCode {
  font-family: "SecretCode", serif;
  font-size: larger;
  font-weight: bold;
}

.AppWrapper{display:flex;flex-direction:column;height:100%}
.Loading-Container .loading{width:1em;height:50px;margin:auto;color:transparent;text-shadow:none;background-color:transparent;border:0;position:relative;padding-left:.5em}.Loading-Container .loading:after{content:"";display:block;position:absolute;font-size:10px;width:1em;height:1em;animation:spinner 2s infinite linear;border-radius:.5em;box-shadow:#9679cd 1.5em 0 0 0,#9679cd 1.1em 1.1em 0 0,#9679cd 0 1.5em 0 0,#9679cd -1.1em 1.1em 0 0,#9679cd -1.5em 0 0 0,#9679cd -1.1em -1.1em 0 0,#9679cd 0 -1.5em 0 0,#9679cd 1.1em -1.1em 0 0}@keyframes spinner{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}
.Standard-Wrapper{display:flex;flex-direction:column;height:100%;overflow-x:hidden}
.Breadcrumb-Outer{margin:0 auto;max-width:1200px;position:relative}.Breadcrumb-Inner{position:absolute;top:5px;left:10px;background-color:#fff;border-radius:20px;z-index:100}.Breadcrumb-Text{padding-right:5px}.Breadcrumb-Text:not(:last-of-type):after{content:">";margin-left:5px}.Breadcrumb-Link{display:inline-block;height:100%;padding:10px;-webkit-text-decoration-color:#381223;text-decoration-color:#381223}.Breadcrumb-Link span{color:#381223}.Breadcrumb-Link:focus,.Breadcrumb-Link:hover{-webkit-text-decoration-color:#625ace;text-decoration-color:#625ace}.Breadcrumb-Link:focus span,.Breadcrumb-Link:hover span{color:#625ace}
.FooterContainer{background-color:#381223;overflow:hidden;color:#fff;position:relative;min-height:100px}@media screen and (min-width: 550px){.FooterContainer{min-height:68px}}@media screen and (min-width: 700px){.FooterContainer{min-height:58px}}.FooterContainer .CopyrightContainer{text-align:center;transform:rotate(-4deg);margin:20px}@media screen and (max-width: 700px){.FooterContainer .CopyrightContainer{padding-bottom:25px}}.FooterContainer .PrivacyPolicyContainer{position:absolute;right:20px;bottom:20px}.FooterContainer .PrivacyPolicyContainer a{color:#fff}
.HeaderContainer{background-color:#625ace}.HeaderContainer .HeaderInner{max-width:1200px;margin:0 auto;display:flex;flex-direction:row;justify-content:space-between}.HeaderContainer a{text-decoration:none}.HeaderContainer a .HeaderLogoContainer{display:flex;flex-direction:row;align-items:center;padding:10px;cursor:pointer;transition:text-shadow .5s ease;text-shadow:none}.HeaderContainer a:hover .HeaderLogoContainer,.HeaderContainer a:focus .HeaderLogoContainer{text-shadow:0 0 2px #e98a81}.HeaderNavigationList{list-style:none;display:flex;margin:0 10px;height:100%;align-items:center;justify-content:center;flex-wrap:wrap;padding-left:0}.HeaderTitle{font-size:x-large;margin:10px;color:#fff}@media screen and (max-width: 800px){.HeaderContainer .HeaderInner{display:flex;flex-direction:column;align-items:center;padding:10px;cursor:pointer}.HeaderLogoContainer{padding-top:0}.HeaderNavigationList li{padding-bottom:5px}}
.HeaderButtonContainer{text-decoration:none;color:#fff}.HeaderButtonContainer .HeaderButton{border:#f4d5a1 1px solid;border-radius:6px;padding:6px 12px;margin:0 4px;transition:.3s}.HeaderButtonContainer:hover .HeaderButton,.HeaderButtonContainer:focus .HeaderButton{background-color:#542860;cursor:pointer}

.ChristmasLights-Container{position:relative}.ChristmasLights-Container body{background:#000}.ChristmasLights-Container .lightrope{text-align:center;white-space:nowrap;overflow:hidden;position:absolute;z-index:1;margin:-15px 0 0 0;padding:0;pointer-events:none;width:100%}.ChristmasLights-Container .lightrope li{position:relative;animation-fill-mode:both;animation-iteration-count:infinite;list-style:none;margin:0;padding:0;display:block;width:12px;height:28px;border-radius:50%;margin:20px;display:inline-block;background:#00f7a5;box-shadow:0px 4.6666666667px 24px 3px #00f7a5;animation-name:flash-1;animation-duration:2s}.ChristmasLights-Container .lightrope li:nth-child(2n+1){background:aqua;box-shadow:0px 4.6666666667px 24px 3px rgba(0,255,255,.5);animation-name:flash-2;animation-duration:.4s}.ChristmasLights-Container .lightrope li:nth-child(4n+2){background:#f70094;box-shadow:0px 4.6666666667px 24px 3px #f70094;animation-name:flash-3;animation-duration:1.1s}.ChristmasLights-Container .lightrope li:nth-child(odd){animation-duration:1.8s}.ChristmasLights-Container .lightrope li:nth-child(3n+1){animation-duration:1.4s}.ChristmasLights-Container .lightrope li:before{content:"";position:absolute;background:#222;width:10px;height:9.3333333333px;border-radius:3px;top:-4.6666666667px;left:1px}.ChristmasLights-Container .lightrope li:after{content:"";top:-14px;position:absolute;width:52px;height:18.6666666667px;border:1px solid #625ace;border-bottom:solid #222 2px;border-radius:50%;z-index:-1}.ChristmasLights-Container .lightrope li:last-child:after{content:none}.ChristmasLights-Container .lightrope li:first-child{margin-left:-40px}@keyframes flash-1{0%,100%{background:#00f7a5;box-shadow:0px 4.6666666667px 24px 3px #00f7a5}50%{background:rgba(0,247,165,.4);box-shadow:0px 4.6666666667px 24px 3px rgba(0,247,165,.2)}}@keyframes flash-2{0%,100%{background:aqua;box-shadow:0px 4.6666666667px 24px 3px aqua}50%{background:rgba(0,255,255,.4);box-shadow:0px 4.6666666667px 24px 3px rgba(0,255,255,.2)}}@keyframes flash-3{0%,100%{background:#f70094;box-shadow:0px 4.6666666667px 24px 3px #f70094}50%{background:rgba(247,0,148,.4);box-shadow:0px 4.6666666667px 24px 3px rgba(247,0,148,.2)}}
.Lanterns-Container{position:relative;margin-bottom:-68px;z-index:100}.Lanterns-Container .lines{margin:auto;padding-top:8px;max-width:1200px;display:flex;justify-content:space-evenly}.Lanterns-Container .lantern{display:inline-block;position:relative;top:0;left:0%;width:40px;height:60px;background-color:rgba(255,255,255,.3);border-top:#facc43 solid 20px;border-bottom:#facc43 solid 5px;border-left:#facc43 solid 2px;border-right:#facc43 solid 2px;border-top-left-radius:30px;border-top-right-radius:30px;transform-origin:top;transition:all 2s}.Lanterns-Container .lantern.extra{display:none}@media screen and (min-width: 800px){.Lanterns-Container .lantern.extra{display:inline-block}}.Lanterns-Container .lantern .hook{position:absolute;top:-25px;left:13px;width:10px;height:0px;background-color:#ff0;border-top:#facc43 solid 10px;border-top-left-radius:10px;border-top-right-radius:10px}.Lanterns-Container .lantern .hook:before{content:"";position:absolute;top:-15px;left:5px;width:1px;height:5px;background-color:#facc43}.Lanterns-Container .lantern .light{position:absolute;margin:auto;top:0;bottom:0;left:0;right:0;width:0px;height:0px;border-radius:50%;box-shadow:0 0 25px 15px #ff0;background-color:#ff0}.Lanterns-Container .lantern:hover{transform:rotate(20deg)}.Lanterns-Container .lantern:nth-child(even):hover{transform:rotate(-20deg)}.Lanterns-Container .lantern:nth-child(even) .light{box-shadow:0 0 25px 15px red;background-color:red}
.IeWarning-Container{text-align:center;border:5px red solid;background-color:pink;padding:50px;font-weight:bold;font-size:larger}
.LinkModal-Container{opacity:0;z-index:-1;visibility:hidden;transition:500ms}.LinkModal-Container.Showing{position:fixed;visibility:visible;opacity:1;left:0;right:0;top:0;bottom:0;z-index:999;background-color:rgba(255,255,255,.5)}.LinkModal-Container.Showing .LinkModal-Content{width:min(500px,90%);margin:auto;margin-top:25vh;padding:20px;background-color:#9679cd;border-radius:20px}.LinkModal-Container.Showing .LinkModal-Content .Description-Container{text-align:center;margin:10px;color:#fff;font-weight:bold}.LinkModal-Container.Showing .Button-Container{padding:10px}.LinkModal-Container.Showing .Button-Container button,.LinkModal-Container.Showing .Button-Container a{font-weight:bold;text-decoration:none;border:2px #fff solid;text-align:center;padding:10px;display:block;width:100%;margin-bottom:10px;color:#fff;border-radius:10px;cursor:pointer;background-color:#9679cd;overflow-wrap:break-word}.LinkModal-Container.Showing .Button-Container button:focus,.LinkModal-Container.Showing .Button-Container button:hover,.LinkModal-Container.Showing .Button-Container a:focus,.LinkModal-Container.Showing .Button-Container a:hover{background-color:#8766c6}.LinkModal-Container.Showing .Button-Container a{background-color:#f4d5a1;color:#000}.LinkModal-Container.Showing .Button-Container a:focus,.LinkModal-Container.Showing .Button-Container a:hover{background-color:#f7dfb8}
.AppContentContainer{flex:1 1;position:relative}.AppContentContainer:focus{outline:none}
